.buildWrapper {
  margin-top: 10px;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 60px;
}


.buildItem {
  max-width: 300px;
  padding: 10px;
  font-weight: 500;
}

@media screen and (max-width: 510px) {
  .buildWrapper {
    padding: 0 15px;
    justify-items: center;
  }
  .buildItem {
    width: 100%;
  }
}

.buildItem:hover {
  cursor: pointer;
  border-radius: 10px;
  background-color: rgba(71, 71, 71, 0.4);
}

.buildItemTitle {
  margin: 0;
  margin-bottom: 5px;
  font-size: 22px;
}

.link {
  text-decoration: none;
}

